@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
.App {
  width: 75vw;
  margin: 0 auto;
  text-align: center;
  font-family: "Poppins", sans-serif;
}

a {
  text-decoration: none;
  color: rgb(2, 105, 2);
}

.post{padding:10px;border:1px solid;border-radius:10px;text-align:left;box-shadow:1px 1px 17px 0 rgba(0,0,0,0.75)}.post__text{text-align:justify}.post__author{display:flex;justify-content:flex-end;font-style:italic}.post__author-name{margin-right:10px}.post__author-email{margin-right:10px}.post__comments-section{display:grid;grid-row-gap:10px}.post__comment{background-color:#bdd8b0;border-radius:10px;padding:7px}.post__comment-body{text-align:justify}.post__comment-author{display:flex;justify-content:flex-end;font-style:italic}.post__comment-author-email{margin-right:10px}.post__comment-author-name{margin-right:10px}

.container{display:grid;grid-template-columns:1fr;grid-gap:20px}

